.page {
  font-size: 2.4154589372vw;
  line-height: 2.4154589372vw;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  .page {
    font-size: 0.6944444444vw;
    line-height: 0.6944444444vw;
  }
}

.page__body {
  margin: 0;
  background-color: #fff;
  font-family: "e-Ukraine", "Arial", sans-seriv;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

button {
  font: inherit;
  background-color: transparent;
  border: none;
  padding: 0;
}

input {
  padding: 0;
  border: none;
  font: inherit;
}

.visually-hidden {
  position: absolute;
  width: 0.0694444444vw;
  height: 0.0694444444vw;
  margin: -0.0694444444vw;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  visibility: hidden;
}